/*
Template Name: Minton - Bootstrap 4 Admin Dashboard
Author: Coderthemes
Email: coderthemes@gmail.com
File: Main Css
*/
/*===================================
  Menu
  ===================================*/
.topbar {
  left: 0;
  position: fixed;
  right: 0;
  top: 0;
  z-index: 999;
}

.topbar .topbar-left {
  float: left;
  position: relative;
  width: 240px;
  z-index: 1;
  background-color: #343c49;
  max-height: 70px;
}

.topbar .topbar-left-sm {
  width: 180px;
}

.topbar .topbar-left-sm + .navbar-custom {
  margin-left: 180px;
}

.logo {
  color: #ffffff !important;
  font-size: 20px;
  font-weight: 700;
  letter-spacing: .05em;
  line-height: 70px;
  text-transform: uppercase;
}

.logo h1 {
  height: 50px;
  margin: 0 auto;
  text-align: center;
}

.logo i {
  color: #039cfd;
}

.logo .icon-c-logo {
  display: none;
}

.navbar-custom {
  background-color: #343c49;
  border-radius: 0;
  margin-bottom: 0;
  padding: 0 10px 0 0;
  margin-left: 240px;
  min-height: 70px;
}

.navbar-custom .nav-link {
  padding: 0;
  line-height: 70px;
  color: rgba(255, 255, 255, 0.6);
}

.navbar-custom .dropdown-toggle:after {
  content: normal;
  content: initial;
}

.navbar-custom .menu-left {
  overflow: hidden;
}

.profile img {
  border: 2px solid #edf0f0;
  height: 36px;
  width: 36px;
}

/* Notification */
.notification-list {
  margin-left: 0 !important;
}

.notification-list .noti-title {
  margin-bottom: 0;
  width: auto;
  padding: 12px 20px;
}

.notification-list .noti-title h5 {
  margin: 0;
}

.notification-list .noti-title .label {
  float: right;
}

.notification-list .noti-icon {
  font-size: 18px;
  padding: 0 12px;
  vertical-align: middle;
  color: rgba(255, 255, 255, 0.8);
}

.notification-list .noti-icon-badge {
  display: inline-block;
  position: absolute;
  top: 14px;
  right: 8px;
}

.notification-list .notify-item {
  padding: 10px 20px;
}

.notification-list .notify-item .notify-icon {
  float: left;
  height: 36px;
  width: 36px;
  line-height: 36px;
  text-align: center;
  margin-right: 10px;
  border-radius: 50%;
  color: #ffffff;
}

.notification-list .notify-item .notify-icon img {
  margin-top: 4px;
}

.notification-list .notify-item .notify-details {
  margin-bottom: 0;
  overflow: hidden;
  margin-left: 45px;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.notification-list .notify-item .notify-details b {
  font-weight: 500;
}

.notification-list .notify-item .notify-details small {
  display: block;
}

.notification-list .notify-item .notify-details span {
  display: block;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  font-size: 13px;
}

.notification-list .notify-all {
  border-radius: 0 0 0.25rem 0.25rem;
  margin: 0 0 -5px 0;
  background-color: #eeeeee;
}

.notification-list .profile-dropdown .notify-item {
  padding: 4px 20px;
}

.profile-dropdown {
  width: 170px;
}

.profile-dropdown i {
  font-size: 17px;
  vertical-align: middle;
  margin-right: 5px;
}

.profile-dropdown span {
  vertical-align: middle;
}

.nav-user {
  padding: 0 12px !important;
}

.nav-user img {
  height: 36px;
  width: 36px;
}

.navbar-nav {
  margin: 0;
}

.side-menu {
  bottom: 0;
  top: 0;
  width: 240px;
  z-index: 2;
}

.side-menu.left {
  background: #ffffff;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.1);
  position: absolute;
  top: 70px;
}

body.fixed-left .side-menu.left {
  bottom: 50px;
  height: 100%;
  margin-bottom: -70px;
  margin-top: 0;
  padding-bottom: 70px;
  position: fixed;
}

.content-page {
  margin-left: 240px;
  overflow: hidden;
}

.content-page > .content {
  margin-bottom: 60px;
  margin-top: 70px;
  padding: 20px 5px 15px 5px;
}

.button-menu-mobile {
  border: none;
  color: #ffffff;
  display: inline-block;
  height: 70px;
  width: 60px;
  background-color: #343c49;
  font-size: 24px;
  z-index: 1;
}

.sidebar-inner {
  height: 100%;
}

#sidebar-menu, #sidebar-menu ul, #sidebar-menu li, #sidebar-menu a {
  border: 0;
  font-weight: normal;
  line-height: 1;
  list-style: none;
  margin: 0;
  padding: 0;
  position: relative;
  text-decoration: none;
}

#sidebar-menu {
  padding-bottom: 30px;
  padding-top: 22px;
  width: 100%;
}

#sidebar-menu .nav > li > a .badge {
  position: absolute;
  right: 10px;
  top: 12px;
}

#sidebar-menu a {
  line-height: 1.3;
}

#sidebar-menu ul ul {
  display: none;
}

#sidebar-menu ul ul li {
  border-top: 0;
}

#sidebar-menu ul ul li.active a {
  color: #039cfd;
}

#sidebar-menu ul ul a {
  color: #75798B;
  display: block;
  padding: 10px 20px 10px 65px;
  font-size: 0.9rem;
}

#sidebar-menu ul ul a:hover {
  color: #039cfd;
}

#sidebar-menu ul ul a i {
  margin-right: 5px;
}

#sidebar-menu ul ul ul a {
  padding-left: 80px;
}

#sidebar-menu .label {
  margin-top: 2px;
}

#sidebar-menu .subdrop {
  color: #039cfd;
}

#sidebar-menu > ul > li > a {
  color: #343c49;
  display: block;
  padding: 12px 20px;
  margin: 4px 0;
  border-left: 3px solid #ffffff;
}

#sidebar-menu > ul > li > a:hover {
  color: #039cfd;
  text-decoration: none;
}

#sidebar-menu > ul > li > a > span {
  vertical-align: middle;
}

#sidebar-menu ul li .menu-arrow {
  -webkit-transition: -webkit-transform .15s;
  transition: -webkit-transform .15s;
  transition: transform .15s;
  transition: transform .15s, -webkit-transform .15s;
  position: absolute;
  right: 20px;
  display: inline-block;
  font-family: "Material Design Icons";
  text-rendering: auto;
  line-height: 24px;
  font-size: 16px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-transform: translate(0, 0);
  transform: translate(0, 0);
  color: #80898e;
}

#sidebar-menu ul li .menu-arrow:before {
  content: "\F142";
}

#sidebar-menu ul li a.subdrop .menu-arrow {
  -webkit-transform: rotate(90deg);
  transform: rotate(90deg);
}

#sidebar-menu ul li a i {
  display: inline-block;
  font-size: 16px;
  line-height: 17px;
  margin-left: 3px;
  margin-right: 15px;
  text-align: center;
  vertical-align: middle;
  width: 20px;
}

#sidebar-menu ul li a i.md {
  font-size: 18px;
}

#sidebar-menu > ul > li > a > i.i-right {
  float: right;
  margin: 3px 0 0 0;
}

#sidebar-menu > ul > li > a.active {
  background: #f5f5f5 !important;
  border-left: 3px solid #039cfd;
}

.menu-title {
  padding: 12px 20px !important;
  letter-spacing: .035em;
  pointer-events: none;
  cursor: default;
  font-size: 13px;
}

.page-title {
  margin-bottom: 0;
  margin-top: 0;
  font-size: 1.2rem;
  float: left;
}

.page-title-box {
  background: #ffffff;
  box-shadow: 0 2px 2px rgba(0, 0, 0, 0.03), 0 1px 0 rgba(0, 0, 0, 0.03);
  padding: 20px;
  margin: -20px -20px 22px -20px;
}

.page-title-box .breadcrumb {
  margin: 0;
  padding: 0;
}

/* Small Menu */
.side-menu-sm {
  width: 180px;
  text-align: center;
}

.side-menu-sm #sidebar-menu > ul > li > a > i {
  display: block;
  font-size: 18px;
  line-height: 24px;
  width: 100%;
  margin: 0;
}

.side-menu-sm #sidebar-menu ul ul a {
  padding: 10px 20px 10px 20px;
}

.side-menu-sm + .content-page .footer {
  left: 180px;
}

#wrapper.enlarged .side-menu-sm {
  text-align: left;
}

#wrapper.enlarged .side-menu-sm #sidebar-menu ul li a i {
  display: inline-block;
  font-size: 18px;
  line-height: 17px;
  margin-left: 3px;
  margin-right: 15px;
  vertical-align: middle;
  width: 20px;
}

.side-menu-sm + .content-page {
  margin-left: 180px;
}

/* Header 2 */
.page-header-2 {
  background: #ffffff;
  border-bottom: 1px solid #dee2e8;
  margin: -20px -15px 22px -15px;
  padding: 10px 20px 0 20px;
}

#wrapper.enlarged .navbar-custom {
  margin-left: 70px;
}

#wrapper.enlarged .menu-title, #wrapper.enlarged .menu-arrow {
  display: none !important;
}

#wrapper.enlarged #sidebar-menu ul ul {
  border: 2px solid #eeeeee;
  margin-top: -5px;
  padding-top: 5px;
  z-index: 9999;
  background-color: #ffffff;
}

#wrapper.enlarged .left.side-menu {
  width: 70px;
  z-index: 5;
}

#wrapper.enlarged .left.side-menu #sidebar-menu > ul > li > a {
  padding: 15px 20px;
  min-height: 52px;
}

#wrapper.enlarged .left.side-menu #sidebar-menu > ul > li > a:hover {
  background: #eeeeee !important;
}

#wrapper.enlarged .left.side-menu #sidebar-menu > ul > li > a:active {
  background: #eeeeee !important;
}

#wrapper.enlarged .left.side-menu #sidebar-menu > ul > li > a:focus {
  background: #eeeeee !important;
}

#wrapper.enlarged .left.side-menu #sidebar-menu > ul > li > a i {
  margin-right: 20px !important;
  font-size: 20px;
}

#wrapper.enlarged .left.side-menu .label {
  position: absolute;
  top: 5px;
  left: 35px;
  text-indent: 0;
  display: block !important;
  padding: .2em .6em .3em !important;
}

#wrapper.enlarged .left.side-menu #sidebar-menu ul > li {
  position: relative;
  white-space: nowrap;
}

#wrapper.enlarged .left.side-menu #sidebar-menu ul > li:hover > a {
  position: relative;
  width: 260px;
  background: #eeeeee;
  color: #039cfd;
